  }
  
  /* Hide the browser's default radio button */
  .ProfileForm_container__X05gv input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
  }
  
  /* Create a custom radio button */
  .ProfileForm_checkmark__3Bpxl {
    position: absolute;
    top: 0;
    left: 0;
    height: 17px;
    width: 17px;
    background-color: white;
    border: 1px solid #C1CFE0;
    border-radius: 50%;
  }
   
  .ProfileForm_container__X05gv input:checked ~ .ProfileForm_checkmark__3Bpxl {
    background: #4E86E4;
    border: 3px solid #C1CFE0;
    box-sizing: border-box;
  }
  
  /* Create the indicator (the dot/circle - hidden when not checked) */
  .ProfileForm_checkmark__3Bpxl:after {
    content: "";
    position: absolute;
    display: none;
  }
  
  /* Show the indicator (dot/circle) when checked */
  .ProfileForm_container__X05gv input:checked ~ .ProfileForm_checkmark__3Bpxl:after {
    display: block;
  }
  

